html {
  box-sizing: border-box; }

*,
*:before,
*:after {
  margin: 0;
  padding: 0;
  box-sizing: inherit; }

body {
  font-family: "Roboto", sans-serif;
  color: #fff;
  background-color: #037529;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center; }

.drink {
  width: 100%;
  padding: 30px 0 50px 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center; }
  .drink__header {
    margin-bottom: 30px;
    max-width: 240px;
    font-size: 30px;
    line-height: 140%;
    text-transform: uppercase;
    text-align: center; }
  .drink__glass {
    position: relative;
    margin-bottom: 10px; }
  .drink__image {
    width: 100%; }
  .drink__number {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    padding-top: 30px;
    font-size: 112px;
    color: #000;
    font-weight: bold; }
  .drink__button {
    margin-top: 30px;
    width: 216px;
    padding: 12px 0 12px 0;
    border-style: none;
    background-color: #ECEFF1; }
    .drink__button--1 {
      border-radius: 4px;
      box-shadow: 1px 4px 8px 3px rgba(0, 0, 0, 0.3);
      font-size: 18px;
      color: #000;
      transition: 0.3s; }
      .drink__button--1:hover {
        box-shadow: 1px 4px 8px 4px rgba(0, 0, 0, 0.4);
        transform: translateY(-3px); }
    .drink__button--2 {
      background-color: #037529;
      color: #ECEFF1;
      text-shadow: 1px 4px 7px rgba(0, 0, 0, 0.9);
      transition: 0.3s; }
      .drink__button--2:hover {
        color: #000;
        text-shadow: none; }
    .drink__button--3 {
      width: 170px;
      color: #444;
      background-color: #abd4b9; }

.history {
  min-height: 100px;
  text-align: center;
  margin-bottom: 40px; }
  .history__header {
    margin: 0 15px 10px 15px; }
  .history__list {
    list-style: none; }
  .history__item {
    padding: 10px 0 10px; }
